<template>
  <div>
    <search-filter @search="searchParams=$event" :placeholder="searchPlaceholder"></search-filter>
    <table class="mdl-data-table mdl-js-data-table">
      <thead>
        <tr>
          <th v-for="column in columns" :key="column.label" class="mdl-data-table__cell--non-numeric">{{column.label}}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in data" :key="item.id" @click="emitSelect(item)">
          <td class="mdl-data-table__cell--non-numeric" v-for="column in columns" :key="column.field">
            <span v-if="column.type === Boolean">
              <i class="material-icons" v-if="item.is_active">visibility</i>
              <i class="material-icons" v-else>visibility_off</i>
            </span>
            <span v-else>
              {{item[column.field]}}
            </span>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :totalItems="count" @pagination="paginationParams=$event"> </pagination>
  </div>
</template>
<script>
import { SearchFilter } from 'SHARED/search'
import { Pagination } from 'SHARED/pagination'

export default {
  name: 'data-table',
  components: {
    SearchFilter,
    Pagination
  },
  watch: {
    paginationParams (value) {
      this.fetchData()
    },
    searchParams (value) {
      this.fetchData()
    }
  },
  props: {
    data: {
      required: true,
      type: Array,
      default: []
    },
    count: {
      type: Number,
      default: 0
    },
    columns: {
      type: Array,
      default: []
    },
    linkColumn: {
      type: Object
    },
    actionMethod: {
      type: Function
    },
    searchPlaceholder: {
      type: String,
      default: 'Search'
    }
  },
  computed: {
    searchParams: {
      get () {
        return this.params.search
      },
      set (value) {
        this.$set(this.params, 'search', value)
        this.$set(this.params, 'page', 1)
      }
    },
    paginationParams: {
      get () {
        return {
          page_size: this.params.page_size,
          page: this.params.page
        }
      },
      set (value) {
        this.$set(this.params, 'page_size', value.page_size)
        this.$set(this.params, 'page', value.page)
      }
    }
  },
  methods: {
    fetchData () {
      this.actionMethod(this.params)
    },
    emitSelect (item) {
      this.$emit('rowSelected', item)
    }
  },
  data () {
    return {
      searchValue: '',
      params: {
        search: '',
        page_size: '',
        page: ''
      }
    }
  }
}
</script>



